.listCehGroupBox:last-child{
    margin-bottom: 50px;
}
.cehGroupBox{
    margin-bottom: 20px;
}
.listBox{
    grid-template-columns: 49%  49%;
    display: grid;
    grid-gap: 10px;
    grid-auto-rows: minmax(200px, auto);
}
.listBox > * {
    max-width: 280px; 
}

.learnListBox{
    grid-template-columns: 100%;
    display: grid;
    grid-gap: 10px;
}
.titleCeh{
    font-family: var(--family);
    font-weight: 700;
    margin-bottom: 10px;
}
.buttonGroup{
    position: fixed;
    z-index: 799;
    bottom: 10px;
    left: 15px;
}
.toggleButton{
    font-family: var(--family);
    position: absolute;
    padding:1px 30px 1px 30px !important;
    border-radius: 10px;
}